.hero {
  background-color: $veryLightGray;
  .container {
    @include breakpoint-up(large) {
      display: flex;
      align-items: center;

      max-width: 86.375rem;
      // max-width: 101.375rem;
    }
  }

  &__image {
    position: relative;
    background-image: url("/images/bg-intro-mobile.svg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    min-height: 17.5rem;

    @include breakpoint-up(medium) {
      min-height: 25rem;
      background-position: center 45%;
    }

    @include breakpoint-up(large) {
      flex: 3;
      order: 2;
      height: 41rem;
      background-image: none;
    }
    &::before {
      @include breakpoint-up(large) {
        content: "";
        position: absolute;
        height: 100%;
        background-image: url("/images/bg-intro-desktop.svg");
        background-repeat: no-repeat;
        background-position: center bottom;
        width: 172%;
        background-size: 58%;
        background-position: 25% 322%;
      }
    }
    &::after {
      content: "";
      position: absolute;
      height: 100%;
      width: 100%;
      // background-image: url("/images/image-plane.jpg");
      background-size: 74%;
      background-repeat: no-repeat;
      background-position: center bottom;
      background-image: url("/images/masaje.svg");

      @include breakpoint-up(medium) {
        background-size: 52%;
      }
      @include breakpoint-up(large) {
        // width: 120%;
        // height: 122%;
        // background-size: 94%;
        // background-position: center 150%;
        width: 92%;
        height: 68%;
        // background-size: 77%;
        background-position: center 94%;
        left: 22%;
      }
    }
  }

  &__text {
    text-align: center;

    @include breakpoint-up(large) {
      flex: 2;
      order: 1;
      text-align: left;
    }
  }
}
